t;\r\n    flex-wrap: wrap !important;\r\n    gap: 2px !important;\r\n    justify-content: center !important;\r\n  }\r\n  \r\n  /* Botones de vista ms compactos en mvil */\r\n  .fc-button-group .fc-button {\r\n    min-width: auto !important;\r\n    white-space: nowrap !important;\r\n    overflow: hidden !important;\r\n    text-overflow: ellipsis !important;\r\n  }\r\n  \r\n  /* Ttulo ms pequeo en mvil */\r\n  .fc-toolbar-title {\r\n    font-size: 1.1rem !important;\r\n    margin: 0 !important;\r\n    text-align: center !important;\r\n  }\r\n  \r\n  /* Vista de MES optimizada para mvil */\r\n  .fc-daygrid-event {\r\n    font-size: 0.7rem !important;\r\n    padding: 1px 3px !important;\r\n    margin: 1px 0 !important;\r\n    border-radius: 3px !important;\r\n  }\r\n  \r\n  .fc-daygrid-day-number {\r\n    font-size: 0.8rem !important;\r\n    padding: 4px !important;\r\n  }\r\n  \r\n  /* Vista de SEMANA optimizada para mvil */\r\n  .fc-timegrid-slot {\r\n    height: 1.8em !important;\r\n    font-size: 0.7rem !important;\r\n  }\r\n  \r\n  .fc-timegrid-event {\r\n    font-size: 0.65rem !important;\r\n    padding: 1px 2px !important;\r\n    border-radius: 2px !important;\r\n  }\r\n  \r\n  .fc-timegrid-slot-label {\r\n    font-size: 0.65rem !important;\r\n    padding: 0 2px !important;\r\n  }\r\n  \r\n  .fc-timegrid-axis {\r\n    width: 35px !important;\r\n  }\r\n  \r\n  .fc-timegrid-slot-lane {\r\n    border-right: 1px solid #dee2e6 !important;\r\n  }\r\n  \r\n  /* Vista de DA optimizada para mvil */\r\n  .fc-timegrid-col {\r\n    min-width: auto !important;\r\n  }\r\n  \r\n  .fc-timegrid-event-harness {\r\n    margin-right: 2px !important;\r\n  }\r\n  \r\n  /* Headers de tiempo ms compactos */\r\n  .fc-timegrid-slot-label-cushion {\r\n    padding: 2px !important;\r\n  }\r\n  \r\n  /* Scroll horizontal para vista semanal en mvil */\r\n  .fc-timegrid {\r\n    overflow-x: auto !important;\r\n    min-width: 100% !important;\r\n  }\r\n  \r\n  .fc-timegrid-body {\r\n    min-width: 100% !important;\r\n  }\r\n  \r\n  /* Mejorar la visualizacin de todas las horas */\r\n  .fc-timegrid-slots > table {\r\n    width: 100% !important;\r\n  }\r\n  \r\n  /* Vista de LISTA optimizada para mvil */\r\n  .fc-list-event-title {\r\n    font-size: 0.85rem !important;\r\n  }\r\n  \r\n  .fc-list-event-time {\r\n    font-size: 0.75rem !important;\r\n  }\r\n  \r\n  .fc-list-day-text,\r\n  .fc-list-day-side-text {\r\n    font-size: 0.8rem !important;\r\n  }\r\n  \r\n  /* Headers de das en todas las vistas */\r\n  .fc-col-header-cell {\r\n    font-size: 0.75rem !important;\r\n    padding: 6px 2px !important;\r\n  }\r\n  \r\n  /* Scroll horizontal para vista semanal en mvil */\r\n  .fc-timegrid {\r\n    overflow-x: auto !important;\r\n    min-width: 100% !important;\r\n  }\r\n  \r\n  /* Mejorar la navegacin en mvil */\r\n  .fc-prev-button,\r\n  .fc-next-button {\r\n    min-width: 40px !important;\r\n  }\r\n  \r\n  /* Popup de eventos ms grande en mvil */\r\n  .fc-popover {\r\n    max-width: 90vw !important;\r\n    font-size: 0.85rem !important;\r\n  }\r\n  \r\n  /* Ajustar el calendario para mvil */\r\n  #calendar {\r\n    font-size: 0.9rem !important;\r\n  }\r\n}\r\n\r\n/* Mejoras para tablets */\r\n@media (min-width: 768px) and (max-width: 992px) {\r\n  .fc-toolbar-title {\r\n    font-size: 1.5rem !important;\r\n  }\r\n  \r\n  .fc-button {\r\n    padding: 0.375rem 0.75rem !important;\r\n  }\r\n}\r\n\r\n/* Mejoras para desktop - DRAG OPTIMIZADO */\r\n@media (min-width: 992px) {\r\n  body {\r\n    padding: 40px 20px;\r\n  }\r\n  \r\n  #calendar {\r\n    max-width: 1200px;\r\n  }\r\n  \r\n  /* Mejorar la toolbar en desktop */\r\n  .fc-toolbar {\r\n    margin-bottom: 1.5rem !important;\r\n  }\r\n  \r\n  .fc-button {\r\n    font-size: 0.9rem !important;\r\n    padding: 0.5rem 1rem !important;\r\n  }\r\n  \r\n  /* Eventos ms fciles de arrastrar en desktop */\r\n  .fc-event {\r\n    cursor: grab !important;\r\n    transition: transform 0.1s ease !important;\r\n  }\r\n