import { Calendar } from '@fullcalendar/core';
import interactionPlugin from '@fullcalendar/interaction';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import listPlugin from '@fullcalendar/list';
import bootstrap5Plugin from '@fullcalendar/bootstrap5';

// Importar Bootstrap JS - CORREGIDO
import { Modal, Toast } from 'bootstrap';

import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import './index.css';

// Variables globales
let calendar;
let currentEvent = null;
let isEditMode = false;

// Funcin auxiliar para detectar mvil
function isMobileDevice() {
  return window.innerWidth <= 768 || 
         /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
}

document.addEventListener('DOMContentLoaded', function() {
  var calendarEl = document.getElementById('calendar');

  // Detectar si es mvil para una configuracin adaptable
  const isMobile = isMobileDevice();
  
  calendar = new Calendar(calendarEl, {
    plugins: [ interactionPlugin, dayGridPlugin, timeGridPlugin, listPlugin, bootstrap5Plugin ],
    themeSystem: 'bootstrap5',
    
    // --- CONFIGURACIN GENERAL Y RESPONSIVE ---
    headerToolbar: {
      left: 'prev,next today',
      center: 'title',
      right: 'dayGridMonth,timeGridWeek,timeGridDay,listWeek'
    },
    initialView: 'dayGridMonth',
    locale: 'es',
    buttonText: {
      today: 'Hoy',
      month: 'Mes',
      week: 'Semana',
      day: 'Da',
      list: 'Lista'
    },
    height: 'auto',
    navLinks: true,
    nowIndicator: true,
    
    // --- INTERACCIN (DRAG, DROP, RESIZE, SELECT) ---
    editable: true,
    eventStartEditable: true, // Asegura que los eventos se puedan mover
    eventDurationEditable: true, // Asegura que los eventos se puedan redimensionar
    selectable: true,
    selectMirror: true,
    droppable: true,
    dayMaxEventRows: true,
    expandRows: false, // Evita que las filas crezcan indefinidamente
    contentHeight: 'auto', // Ajusta la altura al contenido, pero dentro de los lmites del contenedor
    eventDragMinDistance: 5, // Previene que un clic se confunda con un arrastre

    // Limitar las horas visibles en las vistas de semana y da
    slotMinTime: '07:00:00',
    slotMaxTime: '22:00:00',

    // --- MANEJADORES DE EVENTOS ---

    // Clic en una fecha/hora vaca -> Crear evento
    dateClick: function(info) {
      openCreateEventModal(info.date, null, info.allDay);
    },

    // Clic en un evento existente -> Mostrar detalles
    eventClick: function(info) {
      info.jsEvent.preventDefault(); // Evitar que el navegador siga la URL del evento
      showEventDetails(info.event);
    },

    // Arrastrar y soltar un evento -> Guardar cambio
    eventDrop: function(info) {
      showToast(`Evento "${info.event.title}" movido.`, 'success');
      saveEventsToStorage();
    },

    // Redimensionar un evento -> Guardar cambio
    eventResize: function(info) {
      showToast(`Evento "${info.event.title}" redimensionado.`, 'success');
      saveEventsToStorage();
    },

    // Seleccionar un rango de tiempo -> Crear evento
    select: function(info) {
      openCreateEventModal(info.start, info.end, info.allDay);
      calendar.unselect();
    },

    // --- DATOS ---
    events: loadEventsFromStorage() || [
      {
        id: '1',
        title: 'Evento de Ejemplo',
        start: new Date().toISOString().split('T')[0],
        color: '#28a745',
        description: 'Este es un evento de ejemplo. Haz clic para editarlo!'
      },
      {
        id: '2',
        title: 'Reunin de Trabajo',
        start: new Date(Date.now() + 86400000).toISOString().split('T')[0] + 'T10:00:00',
        end: new Date(Date.now() + 86400000).toISOString().split('T')[0] + 'T11:00:00',
        color: '#dc3545',
        description: 'Reunin semanal del equipo'
      }
    ]
  });

  calendar.render();
  
  initializeEventListeners();
  
  showInitialInstructions();
  
  window.addEventListener('resize', debounce(function() {
    calendar.updateSize();
  }, 250));
});

// Funciones para manejar eventos
function openCreateEventModal(start, end, allDay) {
  isEditMode = false;
  currentEvent = null;
  
  const form = document.getElementById('eventForm');
  if (form) {
    form.reset();
  }

  const modalLabel = document.getElementById('createEventModalLabel');
  if (modalLabel) {
    modalLabel.textContent = 'Nuevo Evento';
  }

  const deleteBtn = document.getElementById('deleteEventBtn');
  if (deleteBtn) {
    deleteBtn.style.display = 'none';
  }

  const startDateInput = document.getElementById('eventStartDate');
  const startTimeInput = document.getElementById('eventStartTime');
  const endDateInput = document.getElementById('eventEndDate');
  const endTimeInput = document.getElementById('eventEndTime');
  const allDayCheckbox = document.getElementById('eventAllDay');

  if (start && startDateInput) {
    startDateInput.value = formatDateForInput(start);
    if (!allDay && startTimeInput) {
      startTimeInput.value = formatTimeForInput(start);
    }
  }

  if (end && endDateInput) {
    endDateInput.value = formatDateForInput(end);
    if (!allDay && endTimeInput) {
      endTimeInput.value = formatTimeForInput(end);
    }
  } else if (start && !allDay && endDateInput && endTimeInput) {
    // Si no hay fecha de fin, agregar 1 hora por defecto
    const defaultEndTime = new Date(start.getTime() + 60 * 60 * 1000);
    endDateInput.value = formatDateForInput(defaultEndTime);
    endTimeInput.value = formatTimeForInput(defaultEndTime);
  }

  if (allDayCheckbox) {
    allDayCheckbox.checked = allDay || false;
  }
  
  toggleTimeInputs();
  
  const modalEl = document.getElementById('createEventModal');
  if (modalEl) {
    const modal = new Modal(modalEl);
    modal.show();
    
    setTimeout(() => {
      const titleInput = document.getElementById('eventTitle');
      if (titleInput) {
        titleInput.focus();
      }
    }, 500);
  }
}

function showEventDetails(event) {
  const modalContent = document.getElementById('eventDetailsContent');
  if (!modalContent) return;

  const startDate = event.start ? new Date(event.start) : null;
  const endDate = event.end ? new Date(event.end) : null;
  
  let detailsHtml = `
    <div class="event-detail-item">
      <div class="event-detail-label">Ttulo:</div>
      <div class="event-detail-value">
        <span class="event-color-preview" style="background-color: ${event.backgroundColor || event.color || '#0d6efd'}"></span>
        ${event.title}
      </div>
    </div>
  `;

  if (event.extendedProps.description) {
    detailsHtml += `
    <div class="event-detail-item">
      <div class="event-detail-label">Descripcin:</div>
      <div class="event-detail-value">${event.extendedProps.description}</div>
    </div>`;
  }

  if (startDate) {
    detailsHtml += `
    <div class="event-detail-item">
      <div class="event-detail-label">Inicio:</div>
      <div class="event-detail-value">
        ${event.allDay ? formatDateForDisplay(startDate) : `${formatDateForDisplay(startDate)} a las ${formatTimeForDisplay(startDate)}`}
      </div>
    </div>`;
  }

  if (endDate) {
    detailsHtml += `
    <div class="event-detail-item">
      <div class="event-detail-label">Fin:</div>
      <div class="event-detail-value">
        ${event.allDay ? formatDateForDisplay(endDate) : `${formatDateForDisplay(endDate)} a las ${formatTimeForDisplay(endDate)}`}
      </div>
    </div>`;
  }

  if (event.url) {
    detailsHtml += `
    <div class="event-detail-item">
      <div class="event-detail-label">URL:</div>
      <div class="event-detail-value">
        <a href="${event.url}" target="_blank" class="text-primary">${event.url}</a>
      </div>
    </div>`;
  }

  modalContent.innerHTML = detailsHtml;
  
  const editEventBtn = document.getElementById('editEventBtn');
  if (editEventBtn) {
    editEventBtn.onclick = function() {
      const viewModalEl = document.getElementById('viewEventModal');
      if (viewModalEl) {
        const viewModal = Modal.getInstance(viewModalEl);
        if (viewModal) {
          viewModal.hide();
        }
      }
      openEditEventModal(event);
    };
  }
  
  const viewModalEl = document.getElementById('viewEventModal');
  if (viewModalEl) {
    const modal = new Modal(viewModalEl);
    modal.show();
  }
}

function openEditEventModal(event) {
  isEditMode = true;
  currentEvent = event;
  
  const form = document.getElementById('eventForm');
  if (form) form.reset();

  const modalLabel = document.getElementById('createEventModalLabel');
  if (modalLabel) modalLabel.textContent = 'Editar Evento';

  const deleteBtn = document.getElementById('deleteEventBtn');
  if (deleteBtn) deleteBtn.style.display = 'inline-block';

  // Llenar formulario con datos del evento
  const titleInput = document.getElementById('eventTitle');
  if (titleInput) titleInput.value = event.title || '';

  const descInput = document.getElementById('eventDescription');
  if (descInput) descInput.value = event.extendedProps.description || '';

  const urlInput = document.getElementById('eventURL');
  if (urlInput) urlInput.value = event.url || '';

  const colorInput = document.getElementById('eventColor');
  if (colorInput) colorInput.value = event.backgroundColor || event.color || '#0d6efd';

  const startDateInput = document.getElementById('eventStartDate');
  const startTimeInput = document.getElementById('eventStartTime');
  const endDateInput = document.getElementById('eventEndDate');
  const endTimeInput = document.getElementById('eventEndTime');
  const allDayCheckbox = document.getElementById('eventAllDay');

  if (event.start && startDateInput) {
    const startDate = new Date(event.start);
    startDateInput.value = formatDateForInput(startDate);
    if (!event.allDay && startTimeInput) {
      startTimeInput.value = formatTimeForInput(startDate);
    }
  }
  
  if (event.end && endDateInput) {
    const endDate = new Date(event.end);
    endDateInput.value = formatDateForInput(endDate);
    if (!event.allDay && endTimeInput) {
      endTimeInput.value = formatTimeForInput(endDate);
    }
  }

  if (allDayCheckbox) {
    allDayCheckbox.checked = event.allDay || false;
  }

  toggleTimeInputs();
  
  const createModalEl = document.getElementById('createEventModal');
  if (createModalEl) {
    const modal = new Modal(createModalEl);
    modal.show();
  }
}

function saveEvent() {
  const form = document.getElementById('eventForm');
  if (!form) return;

  const title = form.eventTitle.value.trim();
  const description = form.eventDescription.value.trim();
  const url = form.eventURL.value.trim();
  const color = form.eventColor.value.trim();
  const allDay = form.eventAllDay.checked;

  let startDate = new Date(form.eventStartDate.value);
  let endDate = new Date(form.eventEndDate.value);

  // Ajustar horas si no es todo el da
  if (!allDay) {
    const startTime = form.eventStartTime.value.split(':');
    const endTime = form.eventEndTime.value.split(':');
    startDate.setHours(startTime[0], startTime[1], 0, 0);
    endDate.setHours(endTime[0], endTime[1], 0, 0);
  } else {
    // Si es todo el da, ajustar a medianoche
    startDate.setHours(0, 0, 0, 0);
    endDate.setHours(23, 59, 59, 999);
  }

  if (isEditMode && currentEvent) {
    // Actualizar evento existente
    updateEvent(currentEvent.id, startDate, endDate, allDay, description, color);
  } else {
    // Crear nuevo evento
    createEvent(startDate, endDate, allDay, description, color);
  }

  // Ocultar modal
  const modal = Modal.getInstance(document.getElementById('createEventModal'));
  if (modal) {
    modal.hide();
  }
}

function deleteEvent() {
  if (!currentEvent) return;
  
  if (confirm('Ests seguro de que quieres eliminar este evento?')) {
    currentEvent.remove();
    saveEventsToStorage();
    showToast('Evento eliminado correctamente', 'success');
    
    const modalEl = document.getElementById('createEventModal');
    if (modalEl) {
      const modal = Modal.getInstance(modalEl);
      if (modal) {
        modal.hide();
      }
    }
  }
}

// Funcin para crear un evento
function createEvent(date, time, allDay, description, color) {
  const title = 'Nuevo Evento';
  const start = new Date(date + 'T' + time);
  const end = new Date(start);
  
  if (allDay) {
    start.setHours(0, 0, 0, 0);
    end.setHours(23, 59, 59, 999);
  } else {
    end.setHours(end.getHours() + 1);
  }
  
  const event = {
    id: Date.now().toString(),
    title,
    start,
    end,
    allDay,
    color,
    description
  };
  
  calendar.addEvent(event);
  saveEventsToStorage();
  showToast('Evento creado.', 'success');
}

// Funcin para actualizar un evento
function updateEvent(id, date, time, allDay, description, color) {
  const event = calendar.getEventById(id);
  if (!event) return;
  
  event.setProp('title', 'Evento Actualizado');
  event.setStart(new Date(date + 'T' + time));
  event.setEnd(new Date(event.start));
  event.setAllDay(allDay);
  event.setExtendedProp('description', description);
  event.setProp('backgroundColor', color);
  event.setProp('borderColor', color);
  
  saveEventsToStorage();
  showToast('Evento actualizado.', 'success');
}

// Funciones de almacenamiento
function saveEventsToStorage() {
  const events = calendar.getEvents().map(event => ({
    id: event.id,
    title: event.title,
    start: event.start ? event.start.toISOString() : null,
    end: event.end ? event.end.toISOString() : null,
    allDay: event.allDay,
    color: event.backgroundColor || event.color,
    url: event.url,
    description: event.extendedProps.description
  }));
  
  localStorage.setItem('fullcalendar_events', JSON.stringify(events));
}

function loadEventsFromStorage() {
  const stored = localStorage.getItem('fullcalendar_events');
  if (!stored) return null;
  try {
    const events = JSON.parse(stored);
    return events.map(e => {
      if (!e) return e;
      // Si ya viene marcado como allDay, respetar
      if (e.allDay === true) return e;
      // Si la fecha de inicio es solo "YYYY-MM-DD", tratar como all-day
      if (typeof e.start === 'string' && e.start.length === 10) {
        return { ...e, allDay: true };
      }
      // Si va de medianoche a medianoche exactas, tratar como all-day
      if (e.start && e.end) {
        const s = new Date(e.start);
        const en = new Date(e.end);
        const isMidnightToMidnight =
          s.getHours() === 0 && s.getMinutes() === 0 &&
          en.getHours() === 0 && en.getMinutes() === 0 &&
          Math.abs(en - s) === 24 * 60 * 60 * 1000;
        if (isMidnightToMidnight) {
          return { ...e, allDay: true };
        }
      }
      return e;
    });
  } catch {
    return null;
  }
}

// Sistema de notificaciones
function showToast(message, type = 'success') {
  // Crear contenedor si no existe
  let container = document.querySelector('.toast-container');
  if (!container) {
    container = document.createElement('div');
    container.className = 'toast-container';
    document.body.appendChild(container);
  }
  
  // Iconos segn el tipo
  const icons = {
    success: 'check-circle',
    error: 'exclamation-circle',
    warning: 'exclamation-triangle',
    info: 'info-circle'
  };
  
  // Crear toast
  const toastEl = document.createElement('div');
  toastEl.className = `toast toast-${type}`;
  toastEl.setAttribute('role', 'alert');
  toastEl.innerHTML = `
    <div class="toast-body d-flex align-items-center">
      <i class="bi bi-${icons[type] || 'info-circle'} me-2"></i>
      ${message}
      <button type="button" class="btn-close ms-auto" data-bs-dismiss="toast"></button>
    </div>
  `;
  
  container.appendChild(toastEl);
  
  // Mostrar toast con duracin segn tipo
  const delay = type === 'info' ? 5000 : 3000;
  const toast = new Toast(toastEl, { delay });
  toast.show();
  
  // Remover despus de ocultar
  toastEl.addEventListener('hidden.bs.toast', () => {
    toastEl.remove();
  });
}

// Utility function
function debounce(func, wait) {
  let timeout;
  return function executedFunction(...args) {
    const later = () => {
      clearTimeout(timeout);
      func(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
  };
}

// Mostrar instrucciones iniciales
function showInitialInstructions() {
  // Solo mostrar si es la primera vez
  if (!localStorage.getItem('fullcalendar_instructions_shown')) {
    setTimeout(() => {
      showToast(' Haz click en cualquier fecha para crear un evento. Arrastra eventos para moverlos.', 'info');
      localStorage.setItem('fullcalendar_instructions_shown', 'true');
    }, 1000);
  }
}

// --- FUNCIONES AUXILIARES ---

function initializeEventListeners() {
  const saveBtn = document.getElementById('saveEventBtn');
  if (saveBtn) saveBtn.addEventListener('click', saveEvent);
  
  const deleteBtn = document.getElementById('deleteEventBtn');
  if (deleteBtn) deleteBtn.addEventListener('click', deleteEvent);
  
  const allDayCheckbox = document.getElementById('eventAllDay');
  if (allDayCheckbox) allDayCheckbox.addEventListener('change', toggleTimeInputs);
  
  const colorSelect = document.getElementById('eventColor');
  if (colorSelect) {
    colorSelect.addEventListener('change', function() {
      this.style.setProperty('--selected-color', this.value);
    });
  }
  
  const eventForm = document.getElementById('eventForm');
  if (eventForm) {
    eventForm.addEventListener('keypress', function(e) {
      if (e.key === 'Enter' && e.target.tagName !== 'TEXTAREA') {
        e.preventDefault();
        saveEvent();
      }
    });
  }
}

function toggleTimeInputs() {
  const allDayCheckbox = document.getElementById('eventAllDay');
  const startTimeInput = document.getElementById('eventStartTime');
  const endTimeInput = document.getElementById('eventEndTime');

  if (!allDayCheckbox || !startTimeInput || !endTimeInput) return;

  const allDay = allDayCheckbox.checked;
  startTimeInput.disabled = allDay;
  endTimeInput.disabled = allDay;

  if (allDay) {
    startTimeInput.value = '';
    endTimeInput.value = '';
  }
}

// Funciones de formato
function formatDateForInput(date) {
  if (!date) return '';
  return date.toISOString().split('T')[0];
}

function formatTimeForInput(date) {
  if (!date) return '';
  return date.toTimeString().slice(0, 5);
}

function formatDateForDisplay(date) {
  return date.toLocaleDateString('es-ES', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });
}

function formatTimeForDisplay(date) {
  return date.toLocaleTimeString('es-ES', {
    hour: '2-digit',
    minute: '2-digit'
  });
}
